.stopwatch {
    text-align: center;
    font-family: Arial, sans-serif;
  }
  
  .time-display {
    font-size: 48px;
    margin-bottom: 20px;
  }
  
  .buttons {
    display: flex;
    justify-content: center;
  }
  
  .button {
    padding: 10px 20px;
    margin: 5px;
    font-size: 16px;
    border: none;
    color: white;
    cursor: pointer;
  }
  
  .stop {
    background-color: red;
  }
  
  .start,
  .reset {
    background-color: black;
  }
  